"use client"

import { Box, Container, Paper } from "@mui/material";
import DiffVariant from "./DiffVariant";
import { useState } from "react";
import CodeViewer from "@/components/common/CodeViewer";
interface TableData {
    id: string;
    name: string;
    type: string;
    default: string | null;
    Description: string;
}

export default function TextFieldComponent() {

    const [DiffVariantCode, setDVCode] = useState<string>(`
          <Box sx={{ display: "flex", flexDirection: "column", gap: "10px" }}>
                            <CustomTextField
                                placeholder="outlined"
                                form={form}
                                formFieldName="outlined"
                                fullWidth={false}
                                useLabel={false}
                                inputTitle="Outlined"
                            />
                            <CustomTextField
                                placeholder="OutLined With Label"
                                form={form}
                                formFieldName="outLinedWithLabel"
                                fullWidth={false}
                                useLabel={true}
                                label="OutLined With Label"
                            />
                        </Box>
                        <Box sx={{ display: "flex", flexDirection: "column", gap: "10px" }}>
                            <CustomTextField
                                placeholder="filled"
                                form={form}
                                formFieldName="filled"
                                fullWidth={false}
                                useLabel={false}
                                inputTitle="Filled"
                            />
                            <CustomTextField
                                placeholder="Filled With Label"
                                form={form}
                                formFieldName="filledWithLabel"
                                fullWidth={false}
                                useLabel={true}
                                label="Filled With Label"
                            />
                        </Box>
                        <Box sx={{ display: "flex", flexDirection: "column", gap: "10px" }}>
                            <CustomTextField
                                placeholder="Standard"
                                form={form}
                                formFieldName="standard"
                                fullWidth={false}
                                useLabel={false}
                                inputTitle="Standard"
                            />
                            <CustomTextField
                                placeholder="Standard With Label"
                                form={form}
                                formFieldName="standardWithLabel"
                                fullWidth={false}
                                useLabel={true}
                                label="Standard With Label"
                            />
                        </Box>
    `)

    const changeDiff = (content:string) =>{
        setDVCode(content);
    }
    return (
        <>
            <Container maxWidth={false}
                sx={{
                    mt: 2,
                    mb: 2,
                    width: '100%',
                    overflow: 'hidden' // Extra safety
                }}>
                <Box
                    sx={{

                        minWidth: '856px', // Adjust width as needed
                        borderRadius: '8px',
                        overflow: 'hidden', // To handle inner borders cleanly
                    }} >
                    <h1>Text Field</h1>
                    Here is the demo of My TextField Component. At the bottom, there are table to show the parameter of that component
                </Box>
                <Box
                    sx={{
                        mt: 4,
                        minWidth: '856px', // Adjust width as needed
                        borderRadius: '8px',
                        overflow: 'hidden', // To handle inner borders cleanly
                    }} >
                    <h2>Basic TextField</h2>
                    <br />
                    There are three variants: outlined (default), filled, and standard
                    <DiffVariant />
                </Box>
                <Box
                    sx={{
                        mt: 2,
                        minWidth: '856px', // Adjust width as needed
                        borderRadius: '8px',
                        overflow: 'hidden', // To handle inner borders cleanly
                    }}
                >
                    <CodeViewer
                    content={DiffVariantCode}
                    setContent={changeDiff}
                    />
                </Box>
            </Container>
        </>
    )
}